<template>
  <div class="container header__inner">
    <router-link to="/">
      <span class="header__logo">
        <img class="header__logo--img" src="../../assets/img/logo.svg" alt="" />
      </span>
    </router-link>
    <span class="header__phones">
      <a href="tel:+996 (502)-34-30-34">+996 (502)-34-30-34</a>
      <a href="tel:+996 (706)-88-40-96">+996 (706)-88-40-96</a>
    </span>
    <button
      @click.prevent="handleChangeActiveNav"
      :class="{ header__burgerActive: isActiveNav }"
      class="header__burger"
      type="button"
    >
      <span class="header__burger-ico"></span> Меню сайта
    </button>
    <router-link to="/quiz">
      <button class="header__apply" type="button">Выбрать программу</button>
    </router-link>
  </div>
</template>

<script>
import { useStore } from "vuex";
import { computed } from "@vue/reactivity";

export default {
  setup() {
    const store = useStore();

    const isActiveNav = computed(() => store.state.navIsActive);

    console.log(isActiveNav.value);

    const handleChangeActiveNav = () => {
      store.dispatch("changeActiveNav");
      console.log(isActiveNav.value);
    };

    return { handleChangeActiveNav, isActiveNav };
  },
};
</script>

<style>
.header__burger {
  cursor: pointer;
}
.header__burgerActive {
  border-color: #fe334a !important;
  background-color: #fe334a !important;
  color: #fff;
}
.nav-sections__mainlink:hover,
.nav-sections__mainlink.active {
  color: #fe334a;
}
</style>
