import { createStore } from 'vuex'

export default createStore({
  state: {
    navIsActive: false,
  },
  mutations: {
    CHANGE_ACTIVE_NAV(state) {
      state.navIsActive = !state.navIsActive
    },
  },
  actions: {
    changeActiveNav({ commit }) {
      commit('CHANGE_ACTIVE_NAV')
    },
  },
  modules: {},
});
