import { createRouter, createWebHistory } from "vue-router";
import Home from "../views/Home.vue";
import College from "../views/College.vue";
import Bakalavr from "../views/Bakalavr.vue";
import Magistr from "../views/Magistr.vue";
import Aspirantura from "../views/Aspirantura.vue";
import Instituts from "../views/Instituts.vue";
import Abituriants from "../views/Abituriants.vue";
import About from "../views/About.vue";
import Tabs from "../components/Tabs.vue";
import Quiz from "../components/quiz/Quiz.vue";

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/college",
    name: "College",
    component: College,
  },
  {
    path: "/bakalavr",
    name: "Bakalavr",
    component: Bakalavr,
  },
  {
    path: "/magistr",
    name: "Magistr",
    component: Magistr,
  },
  {
    path: "/aspirantura",
    name: "Aspirantura",
    component: Aspirantura,
  },
  {
    path: "/instituts",
    name: "Instituts",
    component: Instituts,
  },
  {
    path: "/abituriants",
    name: "Abituriants",
    component: Abituriants,
  },
  {
    path: "/tabs",
    name: "Tabs",
    component: Tabs,
  },
  {
    path: "/about",
    name: "About",
    component: About,
  },
  {
    path: "/quiz",
    name: "Quiz",
    component: Quiz,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
