<template>
  <form
    @submit.prevent="postMail"
    method="post"
    class="form catalog__form"
    novalidate="novalidate"
  >
    <h3>Этот тест подберет вам подходящее направление для вас))</h3>
    <div class="form__fileds">
      <span class="form__filed">
        <input
          v-model="first"
          type="text"
          class="form__input GoodLocal"
          placeholder="Вы склонны больше к профессиям?"
        />
      </span>
      <span class="form__filed">
        <input
          v-model="second"
          type="text"
          class="form__input GoodLocal"
          placeholder="Какое направление обучения вас интересует?"
        />
      </span>
      <span class="form__filed">
        <input
          v-model="third"
          type="text"
          class="form__input GoodLocal"
          placeholder="Какой формат обучения вам подходит?"
        />
      </span>
      <span class="form__filed">
        <input
          v-model="fourth"
          type="text"
          class="form__input GoodLocal"
          placeholder="Нужно ли трудоустройство?"
        />
      </span>
      <span class="form__filed">
        <input
          name="name"
          v-model="name"
          type="text"
          class="form__input GoodLocal"
          placeholder="Имя"
        />
      </span>
      <span class="form__filed">
        <input
          name="phone"
          v-model="phone"
          type="tel"
          class="form__input"
          inputmode="tel"
          placeholder="Телефон"
          data-inputmasks-inited=""
        />
      </span>
      <span class="form__filed">
        <input
          name="email"
          type="email"
          v-model="email"
          class="form__input"
          placeholder="Электронная почта"
        />
      </span>
    </div>
    <div class="form__bottom">
      <button class="button form__button">Отправить ответы</button>
    </div>
  </form>
</template>

<script>
import { ref } from '@vue/reactivity';
export default {
  setup() {
    const first = ref("");
    const second = ref("");
    const third = ref("");
    const fourth = ref("");
    const name = ref("");
    const phone = ref("");
    const email = ref("");

    async function postMail() {
      const response = await fetch(
        "http://localhost:8000/api/auth/quiz",
        {
          method: "POST",
          body: JSON.stringify({
            first: first.value,
            second: second.value,
            third: third.value,
            fourth: fourth.value,
            name: name.value,
            phone: phone.value,
            email: email.value,
          }),
          headers: {
            "Content-Type": "application/json",
          },
        }
      );
      const json = await response.json();
    }

    return {
      first,
      second,
      third,
      fourth,
      name,
      phone,
      email,
      postMail,
    };
  },
};
</script>

<style scoped>
.catalog__form {
  padding: 100px 0;
}
</style>
